/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: #040b16;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

